.contactMe-section{
    padding:50px;
    display:flex;
    flex-flow:column wrap;
    background-color:white;
  }
  
  .hyphen{
    display:block;
    width:26px;
    height:5px;
    background-color:var(--blue-color);
  }
  
  .contactMe-title{
    display:flex;
    flex-flow:row wrap;
    font-weight:400;
    font-size:1.5rem;
    align-items: center;
  }
  
  .contactMe-text h3{
    font-size:1.7rem;
    font-weight:600;
    margin-top:20px;
  }
  
  .contactMe-text h3>span{
    color:var(--orange-color);
    font-weight:400;
  }
  
  .contactMe-text>p{
    font-size:1.15rem;
    line-height:30px;
    margin-top:12px;
    letter-spacing:0.2px;
  }
  
  .contactMe-methods{
    display:flex;
    flex-flow:column wrap;
  }
  .phone-number, .email{
    display:flex;
    flex-flow: row wrap;
    margin-top:20px;
    align-items:center;
  }

  .phone-number img{
    margin:20%;
  }

  .email img{
    margin:25%;
  }
  
  .phone-number p, .email p{
    font-size:1.1rem;
    margin-left:10px;
    letter-spacing:0.3px;
  }

  .phone-icon:before{
    content: "\f2a0";
  }

  .contact-email-icon:before{
    content: "\f0e0";
  }

  @media (min-width:768px){
    .contactMe-section{
      padding:60px 80px;
    }
  }

  @media (min-width:1025px){
    .contactMe-section{
      padding:70px 120px;
      display:flex;
      flex-flow:row wrap;
      justify-content: space-between;
    }

    .contactMe-text{
      flex-basis:35%;
      margin-top:35px;
    }

    .contactMe-title{
      font-size:1.1rem;
    }
    
    .contactMe-text h3{
      font-size:1.3rem;
    }

    .contactMe-text>p{
      font-size:0.9rem;
    }

    .phone-number p, .email p{
      font-size:0.9rem;
    }

    .contactMe-form{
      flex-basis:60%;
      margin-top:-30px;
    }




  }
  
  
