.project-card{
    margin-top:40px;
}

.background-image{
    width:100%;
    height:auto;
    background-color:white;
    border-radius:3px;
    box-shadow: 1px 1px 2px #ccc;
}

.background-image img{
    width:93%;
    margin:4% 3.5%;
    border-radius:3px;
}

.project-technologies{
    display:flex;
    padding:10px;
    justify-content: space-evenly;
    
}

.project-technologies div{
    width:80px;
    height:35px;
    background-color:var(--orange-color);
    text-align: center;
    font-size:1rem;
    line-height:33px;
    font-weight:600;
    box-shadow:1px 2px 4px rgb(0,0,0,0.5);
    opacity:0.6;
}

.project-text{
    padding:10px;
}

.project-text h1{
    font-size:1.5rem;
    font-weight:400;
    letter-spacing:0.5px;
}

.project-text p{
    font-size:1.15rem;
    line-height:25px;
    margin-top:10px;
    letter-spacing:0.2px;
    font-family: "Lato";
}

@media (min-width:1025px) {
    .background-image{
        width:80%;
        margin:0 auto;
    }
    
    .project-technologies{
        width:80%;
        margin:0 auto;
    }

    .project-technologies div{
        font-size:0.9rem;
        width:73px;
        height:30px;
        justify-content: start;
        margin-left:10px;
    }

    .project-text{
        width:80%;
        margin:0 auto;
    }

    .project-text h1{
        font-size:1.1rem;
    }

    .project-text p{
        font-size:0.9rem;
    }
}
